.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.backdrop{
  z-index: 1;
  color: #fff;
}

.layout-container{
  display: flex;
  margin-top: 65px;
  .side-menu-container{
    height: 100vh;
    position: fixed;
    .menu-body{
      height: 100%;
      color: white;
      background-color: #212b51;
      .link-item{
        text-decoration: none;
        color: white;
      }
    }
  }
}