@mixin setGrid($columns, $columns-gap, $rows, $rows-gap, $type) {
    display: grid;
    @if ($type == 'columns') {
      grid-template-columns: $columns;
      grid-column-gap: $columns-gap;
    } @else if ($type == 'rows') {
      grid-template-rows: $rows;
      grid-row-gap: $rows-gap;
    } @else if ($type == 'both') {
      grid-template-columns: $columns;
      grid-column-gap: $columns-gap;
      grid-template-rows: $rows;
      grid-row-gap: $rows-gap;
    }
  }

